header{
    padding: 3px;
    text-align: center;
    z-index: 1;
    top: 0;
    left: 0;
    margin: 0;
}
h3{
    font-family: "Copperplate";
    padding: 0px 0px; 
    display: inline-block;
    text-align: center;
    position: relative;
    margin-top: 3px;
    margin-bottom: 3px;
    text-align: center;
    font-size: 35px ;
    display: flex;
    justify-content: center; 
    align-items: center; 
    max-width: 100%;
}

td{
    font-size: 30px;
    font-family: "Hind Madurai";
    
}

.output{
    font-size: large;
    color: #f76c30;
    background-color: #faf1e0;
    border: none;
    display: inline-block;
    height: 68px;
    width: 375px;
    font-size: 24px;
    margin: 0;
    text-align: right;
}

.button{
    background-color: #faf1e0;
    border-radius: 100%;
    width: 54px;
	height: 54px;
    border: none;
    font-size: 24px;
    font-family: "Hind Madurai";
    margin: 0;
}

.button.calculate{
    background-color: #eedbc4;
}

.button.equal{
    background-color: #135f8b;
    color: white;
}