h1 {
    margin-left: 65px;
    margin-top: -50px;
    font-family: 'Copperplate';
    text-align: center;
}

div#selection {
    margin-left: -260px;
}

select{
    background-color: #C4C4C4;
    color: #E57441
}

select#translating {
    position: relative;
    width: 159px;
    height: 19px;
    top: -7px;
}

p {
    position: relative;
    display: inline;
    margin: 15px;
    top: -7px;
    font-family: 'Courier New';
}

div.skiptranslate.goog-te-gadget {
    display: inline-block;
    border-top: 20px;
    z-index: 2;
}

textarea#input {
    height: 100px;
    width: 700px;
    border-style: solid;
    border-radius: 25px;
    border-width: 1px;
    margin: 20px;
    padding: 20px;
    z-index: 1;
    font-family: 'Courier New';
    resize: none;
    background-color: #F9F1E1;
    color: #E57441;
}

span#google_translate_element{
    position: absolute;
    top: 70px;
}

div#translated {
    height: 100px;
    width: 700px;
    border-style: solid;
    border-radius: 25px;
    border-width: 1px;
    margin: 20px;
    padding: 20px;
    margin-right: 100px;
    background-color: #2D5E87;
    color: white;
}

@media screen and (max-width: 400px){

    textarea#input {
        height: 63px;
        width: 350px;
        border-style: solid;
        border-radius: 25px;
        border-width: 1px;
        margin: 5px;
        padding: 15px;
        z-index: 1;
        font-family: 'Courier New';
        resize: none;

    }

    div#translated{
        height: 126px;
        width: 350px;
        border-style: solid;
        border-radius: 25px;
        border-width: 1px;
        margin: 5px;
        padding: 15px;
        z-index: 1;
    }

    h1{
        margin-left: 0;
        text-align: center;
    }

}