header{
    padding: 3px;
    text-align: center;
    background-color: #135f8b;
    margin-bottom: 5px;
}
h3{
    color: white;
    margin-top: 3px;
    margin-bottom: 3px;
    font-size: 35px;
    font-family: "Copperplate";
}
input{
    border: none;
    background-color: #faf1e0;
    color: #f76c30;
    width: 100%;
    font-size: 25px;
    font-family: "Hind Madurai";
    margin-bottom: 5px;
    text-align: center;
    align-items: center;
}
div#outcome{
    border: none;
    background-color: #faf1e0;
    color: #f76c30;
    width: 100%;
    font-size: 25px;
    font-family: "Hind Madurai";
    margin-bottom: 5px;
}
input.top{
    margin-top: 25px;
}

#exchange{
    width: 100%;
    color: #135f8b;
    margin: 0 auto;
    text-align: center;
    font-family: "Hind Madurai";
    font-size: 25px;
    margin-bottom: 5px;
}

select{
    text-align: center;
    width: 45%;
    display: inline-block;
    font-family: "Hind Madurai";
    font-size: 15px;
    margin-bottom: 5px;
    background-color: #c4c4c4;
    color: #f76c30;

}
#money_type{
    font-size: 100%;
    width: 100%;
    text-align: center;
}

label{
    font-family: "Hind Madurai";
    font-size: 25px;
}